
<template>
    <input type="search" class="input" v-model="inputData" @input="findName()">
    <div>{{data.itemPrevious}}</div>
    <transition name="slide-fade" mode="out-in">
    <ul class="data" v-if="flagTransition">
        <li class="data__item" v-for="item in foundItem" :key="item">
            <router-link :to="{
            name: 'SpeciesPage',
            params: {
              itemRout: `${item.routing}`,
              itemName: `${item.name}`,
              itemDescription: `${item.description}`,
              itemImg: `${item.img}`,
              itemNumber: `${item.number}`,
              itemCoord: `${JSON.stringify(item.coordinates)}`,
              itemPrevious: `${item.previous}`,
              itemRef: item.ref,
              itemInfo: item.status,
            },
          }" class="data__link">{{item.name}}</router-link>
        </li>
    </ul>
    </transition>
</template>


<script>

export default {
        name: "inputSpecies",
        props: {data: Array},
        data() {
            return {
                inputData: null,
                foundItem: new Set(),
                flagTransition: false,
            }
        },
        methods: {
            findName(){
                this.data.forEach(item => {
                    if(this.inputData !== ""){
                    if(item.name.toLowerCase().includes(this.inputData.toLowerCase())){
                        this.foundItem.add(item);
                        this.flagTransition = true;
                    }else this.foundItem.delete(item);
                    }
                    if(this.inputData == ""){
                        this.foundItem = new Set();
                        this.flagTransition = false
                    }     
                })
            },
        }
}
</script>


<style scoped>
.slide-fade-enter-active,
.slide-fade-leave-active {
  transition: opacity 0.5s ease;
}

.slide-fade-enter-from,
.slide-fade-leave-to {
  opacity: 0;
}
    .input{
        margin: 50px 0 0 0;
        width: 200px;
        height: 40px;
        text-align: center;
        border: 3px solid black;
        border-style:dotted;
        outline-color: rgb(117, 15, 15);
    }
    .data{
        background-color: bisque;
        list-style: none;
        margin: 0;
        padding: 0;
        width: 200px;
        max-height: 100px;
        overflow: auto;
    }
    .data__item{
        border: 1px solid black;
        padding: 5px 0;
        font-weight: 700;
        cursor: pointer;
        text-align: center;
    }
    .data__item:not(:last-child){
        margin-bottom: 5px;
    }
    .data__link{
        color: black;
        text-decoration: none;
    }
</style>
